import classNames from 'classnames';
import {string} from 'prop-types';
import styles from './DownloadButton.scss';

export default class DownloadButton extends React.Component {

    state = {downloadUrl: "https://github.com/nos/client/releases"};

    DownloadUrl = async () => {
        let downloadUrl = "https://github.com/nos/client/releases";
        const resp = await fetch('https://api.github.com/repos/nos/client/releases/latest');
        console.log(resp);
        const assets = resp.json().assets;
        assets.forEach(asset => {
            if ((downloadUrl.endsWith(".exe") && navigator.platform.indexOf('Win') !== -1) || (downloadUrl.endsWith(".dmg") && navigator.platform.indexOf('Mac') !== -1) || (downloadUrl.endsWith(".AppImage") && navigator.platform.indexOf('Linux') !== -1)) {
                this.setState({downloadUrl});
            }
        });
    };

    componentDidMount() {
        this.DownloadUrl()
    }


    render() {
        return (
            <a
                className={classNames('button', styles.downloadButton, this.props.className)}
                href={this.state.downloadUrl}
                target="_blank"
            >
                {this.props.label}
            </a>
        )
    }

}

DownloadButton.propTypes = {
    className: string,
    label: string
}

DownloadButton.defaultProps = {
    className: null,
    label: 'Download Now'
}




